import React, { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useNavigate, useSearchParams } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { toast } from 'react-toastify';
import clsx from 'clsx';
import { PARTNERS, NEW_PARTNER } from 'app/constants/routes.constants';
import { IMerchant } from 'shared/interfaces/IMerchant';
import { CustomPagination } from 'shared/ui/Pagination';
import {
  useGetPartnersQuery,
  useUpdatePartnerMutation
} from '../../features/api/partnerApi';
import Filter from './partials/Filter';
import { objectClear } from '../../shared/utils/objectClear';
import {
  IconButton,
  Button,
  Typography,
  Checkbox,
  Skeleton,
  SelectChangeEvent,
  Switch
} from '@mui/material';
import { ChevronDownIcon, ChevronUpIcon } from '../../shared/icons';

interface IFormValues {
  partnerLegalName: string;
  bin: string;
  city: string;
  merchantId: string;
  pointCode: string;
}

const Partners: FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [search, setSearch] = useSearchParams();
  const [isFilterVisible, setIsFilterVisible] = useState(true);
  const [checkedItems, setCheckedItems] = useState<Array<number | string>>([]);
  const [limit, setLimit] = useState(25);
  const [queryParams, setQueryParams] = useState({
    partnerLegalName: search.get('partnerLegalName') ?? '',
    bin: search.get('bin') ?? '',
    city: search.get('city') ?? '',
    merchantId: search.get('merchantId') ?? '',
    pointCode: search.get('pointCode') ?? ''
  });

  const { handleSubmit, control, reset } = useForm({
    defaultValues: {
      ...queryParams,
      status: ''
    }
  });
  const [updatePartner, { isLoading, originalArgs, isError, error }] =
    useUpdatePartnerMutation<any>();
  const {
    data,
    isSuccess,
    isFetching,
    isError: isFetchError,
    error: fetchError
  } = useGetPartnersQuery<any>({
    ...objectClear(queryParams),
    page: search.get('page') || 1,
    limit
  });

  useEffect(() => {
    if (isError) {
      toast.error(error?.data?.errorMessage || t('status.error'), {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        pauseOnHover: true
      });
    }
  }, [isError]);

  useEffect(() => {
    if (isFetchError) {
      toast.error(fetchError?.data?.errorMessage || t('status.error'), {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        pauseOnHover: true
      });
    }
  }, [isFetchError]);

  const onSubmit = handleSubmit((values: IFormValues) => {
    const queryString = new URLSearchParams({
      ...objectClear(values)
    }).toString();
    setSearch(queryString);
    setQueryParams(values);
  });

  const handleFilterToggle = () => {
    setIsFilterVisible(prev => !prev);
  };

  const handlePageChange = (
    event: React.ChangeEvent<unknown>,
    value: number
  ) => {
    setSearch(`?page=${value + 1}`);
  };

  const handleReset = () => {
    setSearch('');
    setQueryParams({
      partnerLegalName: '',
      bin: '',
      city: '',
      merchantId: '',
      pointCode: ''
    });
    reset();
  };

  const handleSelect =
    (id: string | number) =>
    (e:any) => {
      if (e.target.checked) {
        setCheckedItems((prev: Array<number | string>) => [...prev, id]);
      } else {
        setCheckedItems((prev: Array<number | string>) => [
          ...prev.filter(prev2 => prev2 !== id)
        ]);
      }
    };

  const handleSwitch =
    (partner: IMerchant) =>
    (e: any) => {
      e.stopPropagation();
      updatePartner({
        ...partner,
        status: e.target.checked ? 'active' : 'inactive'
      });
    };

  return (
    <>
      <Typography variant="h1">{t('partner.header.title')}</Typography>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transactions.filter.title')}
        </Typography>
        <IconButton aria-label="filterToggle" onClick={handleFilterToggle}>
          {isFilterVisible ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>
      {isFilterVisible && (
        <form onSubmit={onSubmit} className="filter">
          <Filter control={control} />
          <div className="filter__footer">
            <Button type="submit" variant="contained" datatype="primary">
              {t('button.apply')}
            </Button>
            <Button
              type="reset"
              variant="contained"
              datatype="secondary"
              onClick={handleReset}
            >
              {t('button.reset')}
            </Button>
          </div>
        </form>
      )}

      <div style={{ display: 'flex', gap: '16px', marginBottom: '24px' }}>
        <Button
          variant="contained"
          datatype="primary"
          className="mb-24"
          onClick={() => navigate(PARTNERS + NEW_PARTNER)}
        >
          {t('partner.button.addPartner')}
        </Button>
        {checkedItems.length ? (
          <>
            <Button variant="contained" datatype="secondary">
              {t('button.delete')}
            </Button>
            <Button variant="contained" datatype="secondary">
              {t('button.activate')}
            </Button>
            <Button variant="contained" datatype="secondary">
              {t('button.deactivate')}
            </Button>
          </>
        ) : null}
      </div>

      <div className="overflowX mb-24">
        <table className="table">
          <thead>
            <tr>
              <td> </td>
              <td>
                <div>{t('partner.table.companyName')}</div>
              </td>
              <td>
                <div>{t('partner.table.partnerCode')}</div>
              </td>
              <td>
                <div>{t('partner.table.bin')}</div>
              </td>
              <td>
                <div>{t('partner.table.pointCode')}</div>
              </td>
              <td>{t('partner.table.status')}</td>
              <td> </td>
            </tr>
          </thead>
          <tbody>
            {isFetching &&
              Array.from({ length: limit }, (_, index) => (
                <tr key={index}>
                  {Array.from({ length: 7 }, item => (
                    <td key={`${index}${item}`}>
                      <Skeleton variant="text" width="100%" />
                    </td>
                  ))}
                </tr>
              ))}
            {isSuccess &&
              Array.isArray(data) &&
              data.map((item: IMerchant) => {
                const isChecked = checkedItems.includes(item.merchantId);
                return (
                  <tr
                    key={item.merchantId}
                    className={clsx({
                      'table-row-selected': isChecked
                    })}
                  >
                    <td>
                      {isLoading &&
                        originalArgs?.merchantId === item.merchantId && (
                          <div className="table__tr-loading">
                            <span>{t('loading')}</span>
                          </div>
                        )}
                      <Checkbox
                        onChange={handleSelect(item.merchantId)}
                        checked={isChecked}
                      />
                    </td>
                    <td>
                      <Link
                        to={`${PARTNERS}/${item.merchantId}`}
                        className="primary-color"
                      >
                        {item.partnerLegalName}
                      </Link>
                    </td>
                    <td>{item.merchantId}</td>
                    <td>{item.bin}</td>
                    <td>{item.pointCode}</td>
                    <td>{t(`user.status.${item.status}`)}</td>
                    <td>
                      <Switch
                        checked={item.status === 'active'}
                        onChange={handleSwitch(item)}
                      />
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </div>
      <CustomPagination
        limit={limit}
        total={1}
        currentIndex={(Number(search.get('page')) || 1) - 1}
        limitOptions={[20, 25, 30]}
        onChangeLimit={(event: SelectChangeEvent) => {
          if (event.target.value) {
            setLimit(Number(event.target.value) ?? 25);
            setSearch('?page=1');
          }
        }}
        onChangePage={handlePageChange}
      />
    </>
  );
};

export default Partners;
