import React, { FC } from 'react';
import { IconProps } from './Icon.model';

import { Icon } from './Icon';

export const ChevronDownIcon: FC<IconProps> = ({
  color = '#0A1E32',
  width,
  height,
  ...props
}) => {
  return (
    <Icon width={width} height={height} fill="none" {...props}>
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M10.0003 12.0184L4.39084 6L4 6.41938L10.0003 12.8571L16 6.41938L15.6092 6L10.0003 12.0184Z"
        fill={color}
        stroke="#0A1E32"
      />
    </Icon>
  );
};
