import React, { FC, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Trans, useTranslation } from 'react-i18next';
import { SubmitHandler, useForm } from 'react-hook-form';
import { toast } from 'react-toastify';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';

import GeneralInfo from './partials/GeneralInfo';
import LegalAddress from './partials/LegalAddress';
import MailingAddress from './partials/MailingAdress';
import { usePostPartnerMutation } from '../../features/api/partnerApi';
import { PARTNERS } from '../../app/constants/routes.constants';
import { digitRegExp, latinRegExp } from '../../shared/utils/regexp';
import { selectCountries } from '../../features/slices/app-slice';
import { RootStateType } from '../../features/store';
import {
  Button,
  Checkbox,
  CircularProgress,
  FormControlLabel,
  Typography
} from '@mui/material';

type AddressType = {
  type: 'juridical' | 'postal';
  country: string;
  city: string;
  postIndex: string;
  street: string;
  house: string;
  flat: string;
  okato: string;
};

type FormValues = {
  legalEntityForm: string;
  partnerLegalName: string;
  bin: string;
  merchantId: string;
  pointCode: string;
  mSite: string;
  phoneNumber: string;
  archivePassword: string;
  status: 'active';
  Adresses: AddressType[];
};

const validationSchema = yup.object().shape({
  partnerLegalName: yup.string().required('form.errors.required'),
  merchantId: yup
    .string()
    .required('form.errors.required')
    .matches(latinRegExp, 'form.errors.onlyLatin'),
  bin: yup
    .string()
    .required('form.errors.required')
    .min(12, 'form.errors.isNumber12')
    .max(12, 'form.errors.isNumber12')
    .matches(digitRegExp, 'form.errors.isNumber12'),
  Adresses: yup.array().of(
    yup.object().shape({
      city: yup.string().required('form.errors.required'),
      postIndex: yup.string().required('form.errors.required'),
      house: yup.string().required('form.errors.required')
    })
  )
});

const NewPartner: FC = () => {
  const { t } = useTranslation();
  const [countAddress, setCountAddress] = useState<number>(0);
  const [postPartner, { isLoading }] = usePostPartnerMutation();
  const countryData = useSelector((state: RootStateType) =>
    selectCountries(state)
  );

  const { handleSubmit, control, reset } = useForm<FormValues>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      legalEntityForm: '',
      partnerLegalName: '',
      bin: '',
      merchantId: '',
      pointCode: '',
      mSite: '',
      phoneNumber: '',
      archivePassword: '',
      status: 'active',
      Adresses: [
        {
          type: 'juridical',
          country: '',
          city: '',
          postIndex: '',
          street: '',
          house: '',
          flat: '',
          okato: ''
        }
      ]
    }
  });

  const onSubmit: SubmitHandler<FormValues> = values => {
    postPartner({
      ...values,
      Adresses: values.Adresses.map((address: any, index: number) => {
        if (index === 0) {
          return {
            ...address,
            type: 'juridical',
            country: countryData ? countryData[0].value : ''
          };
        }
        return {
          ...address,
          type: 'postal',
          country: countryData ? countryData[0].value : ''
        };
      })
    })
      .then((res: any) => {
        if (res?.error) {
          toast.error(res.error?.data?.errorMessage || t('status.error'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
        } else {
          toast.success(
            <Trans
              i18nKey="partner.new-partner.success.link"
              values={{
                fullName: values?.partnerLegalName
              }}
              components={[
                <Link key="fullName" to={`${PARTNERS}/${values?.merchantId}`}>
                  fullName
                </Link>
              ]}
            />,
            {
              position: 'top-right',
              autoClose: 5000,
              hideProgressBar: false,
              pauseOnHover: true
            }
          );
          reset({
            legalEntityForm: '',
            partnerLegalName: '',
            bin: '',
            merchantId: '',
            pointCode: '',
            mSite: '',
            phoneNumber: '',
            archivePassword: '',
            Adresses: [
              {
                type: 'juridical',
                country: '',
                city: '',
                postIndex: '',
                street: '',
                house: '',
                flat: '',
                okato: ''
              }
            ]
          });
          setCountAddress(0);
        }
      })
      .catch(() => {
        toast.error(t('status.error'), {
          position: 'top-right',
          autoClose: 5000,
          hideProgressBar: false,
          pauseOnHover: true
        });
      });
  };

  const handleIsAddressMatchChange = () => {
    setCountAddress((prev: number) => (prev !== 0 ? 0 : prev + 1));
  };

  const handleAddAddress = () => {
    setCountAddress(prev => prev + 1);
  };

  const generalInfoMemo = useMemo(() => {
    return <GeneralInfo control={control} />;
  }, [control]);

  const legalAddressMemo = useMemo(() => {
    return <LegalAddress control={control} />;
  }, [control]);

  return (
    <>
      <Typography variant="h1">{t('partner.new.header.title')}</Typography>
      <form onSubmit={handleSubmit(onSubmit)} className="mt-24 mb-24">
        {generalInfoMemo}
        {legalAddressMemo}
        <div className="mb-34">
          <FormControlLabel
            control={
              <Checkbox
                onChange={handleIsAddressMatchChange}
                checked={!countAddress}
              />
            }
            label={t('partner.new.form.legal.matchEmail')}
          />
        </div>
        {!!countAddress && <MailingAddress control={control} />}
        <Button
          type="submit"
          variant="contained"
          datatype="primary"
          disabled={isLoading}
        >
          {isLoading ? (
            <CircularProgress color="success" size={16} />
          ) : (
            t('button.save')
          )}
        </Button>
      </form>
    </>
  );
};

export default NewPartner;
