import React from 'react';
import { Navigate } from 'react-router-dom';
import {
  ANALYTICS,
  NEW_PARTNER,
  NEW_USER,
  PARTNERS,
  ROOT,
  SETTINGS,
  SETTLEMENTS,
  STATISTICS,
  TRANSACTIONS,
  USER,
  USERS
} from './constants/routes.constants';
import {
  AnalyticsPage,
  NewPartnerPage,
  NewUserPage,
  PartnersPage,
  SettingsPage,
  StatisticsPage,
  TransactionPage,
  TransactionsPage,
  UserPage,
  UsersPage
} from '../pages';
import { PartnerPage } from '../pages/Partner';

export const routes = [
  {
    path: ROOT,
    element: <Navigate to={TRANSACTIONS} />
  },
  {
    path: PARTNERS,
    element: <PartnersPage />
  },
  {
    path: `${PARTNERS}/:merchantId`,
    element: <PartnerPage />
  },
  {
    path: PARTNERS + NEW_PARTNER,
    element: <NewPartnerPage />
  },
  {
    path: USERS,
    element: <UsersPage />
  },
  {
    path: USER,
    element: <UserPage />
  },
  {
    path: NEW_USER,
    element: <NewUserPage />
  },
  {
    path: TRANSACTIONS,
    element: <TransactionsPage />
  },
  {
    path: `${TRANSACTIONS}/:id`,
    element: <TransactionPage />
  },
  {
    path: ANALYTICS,
    element: <AnalyticsPage />
  },
  {
    path: STATISTICS,
    element: <StatisticsPage />
  },
  {
    path: SETTLEMENTS,
    element: <>SETTLEMENTS</>
  },
  {
    path: SETTINGS,
    element: <SettingsPage />
  }
];
