import React, { FC } from 'react';
import { IconProps } from './Icon.model';

import { Icon } from './Icon';

export const ArrowUpIcon: FC<IconProps> = ({
  color = '#0DBA26',
  width,
  height,
  ...props
}) => {
  return (
    <Icon width={width} height={height} fill="none" {...props}>
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M9 16L9 3.8L14.6 9.4L16 8L8 0L0 8L1.4 9.4L7 3.8L7 16H9Z"
        fill={color}
        fillOpacity="0.95"
      />
    </Icon>
  );
};
