import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Typography from '@mui/material/Typography';
import { Grid, IconButton, Link } from '@mui/material';
import { IClientInfo } from 'shared/interfaces/IOrder';
import { phoneNumberFormatter } from 'shared/utils/formatters';
import { ChevronDownIcon, ChevronUpIcon } from '../../shared/icons';

type PropTypes = {
  item: IClientInfo;
};

const BuyerInfo: FC<PropTypes> = ({ item }) => {
  const { t } = useTranslation();
  const [expanded, setExpanded] = useState(true);

  return (
    <>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transaction.collapse.buyer')}
        </Typography>
        <IconButton
          aria-label="filterToggle"
          onClick={() => setExpanded(prev => !prev)}
        >
          {expanded ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>
      {expanded && (
        <div style={{ backgroundColor: '#F3F4F5', padding: '24px 16px' }}>
          <Grid container>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">{t('user.surname')}:</label>
                <span className="ml-4">
                  {item.cLastName || t('system.param.notSpecified')}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">{t('user.phoneNumber')}:</label>
                <span className="ml-4">
                  <Link href={`tel:${phoneNumberFormatter(item.phoneNumber)}`}>
                    {phoneNumberFormatter(item.phoneNumber)}
                  </Link>
                </span>
              </div>
            </Grid>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">{t('user.name')}:</label>
                <span className="ml-4">
                  {item.cName || t('system.param.notSpecified')}
                </span>
              </div>
            </Grid>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">{t('user.middleName')}:</label>
                <span className="ml-4">
                  {item.cMiddleName || t('system.param.notSpecified')}
                </span>
              </div>
            </Grid>
          </Grid>
        </div>
      )}
    </>
  );
};

export default BuyerInfo;
