import React, { FC, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams, Navigate } from 'react-router-dom';
import { Box, Tab, Tabs } from '@mui/material';
import Information from './partials/Information';
import Users from './partials/Users';
import { PARTNERS } from '../../app/constants/routes.constants';

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

const Partner: FC = () => {
  const { t } = useTranslation();
  const { merchantId } = useParams();
  const [currentTab, setCurrentTab] = useState<number>(1);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setCurrentTab(newValue);
  };

  const tabContentMemo = useMemo(() => {
    if (merchantId) {
      switch (currentTab) {
        case 1:
          return <Information merchantId={merchantId} />;
        case 2:
          return <Users merchantId={merchantId} />;
        default:
          return <Information merchantId={merchantId} />;
      }
    }
    return null;
  }, [currentTab, merchantId]);

  if (!merchantId) {
    return <Navigate to={PARTNERS} />;
  }

  return (
    <>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={currentTab} onChange={handleChange} aria-label="partner">
          <Tab label={t('partner.nav.information')} {...a11yProps(1)} />
          <Tab label={t('partner.nav.users')} {...a11yProps(2)} />
        </Tabs>
      </Box>
      {tabContentMemo}
    </>
  );
};

export default Partner;
