import React, { FC } from 'react';
import AreaChartView from '../../assets/images/area-chart.png';

const AreaChart: FC = () => {
  return (
    <img
      src={AreaChartView}
      loading="lazy"
      alt="area-chart"
      width="100%"
      height="auto"
    />
  );
};

export default AreaChart;
