import React, { FC, useRef } from 'react';
import { Skeleton } from '@mui/material';
import { IProps } from './types';
import s from './Table.module.css';

export const Table: FC<IProps> = ({
  columns,
  dataSource,
  width = '1132px',
  isLoading,
  limit,
  onClick
}) => {
  const tableRef = useRef<HTMLDivElement>(null);

  const gridTemplateColumns = columns
    .map(({ grid }) => (grid ? `${grid}fr` : '1fr'))
    .join(' ');

  return (
    <div className={s.wrapper} ref={tableRef}>
      <div className={s.table_wrapper} style={{ width }}>
        <div className={s.header_wrapper} style={{ gridTemplateColumns }}>
          {columns.map(({ title, key }) => (
            <div key={key} className={s.header_item}>
              {title ?? ''}
            </div>
          ))}
        </div>
        {isLoading
          ? Array.from({ length: limit }).map(number => (
              <div
                key={number as number}
                style={{ gridTemplateColumns }}
                className={s.body_item}
              >
                {columns.map(column => (
                  <div key={column.key}>
                    <Skeleton variant="text" width="100%" />
                  </div>
                ))}
              </div>
            ))
          : dataSource?.map(item => {
              return (
                <div
                  key={item}
                  style={{ gridTemplateColumns }}
                  className={s.body_item}
                >
                  {columns.map(column => {
                    return (
                      <div
                        key={column.dataIndex}
                        onClick={
                          typeof onClick === 'function'
                            ? onClick(item)
                            : undefined
                        }
                      >
                        {typeof column.render === 'function'
                          ? column.render(item[column.dataIndex], item)
                          : item[column.dataIndex]}
                      </div>
                    );
                  })}
                </div>
              );
            })}
      </div>
    </div>
  );
};
