import React, { FC } from 'react';
import { Controller, SubmitHandler, useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import { Trans, useTranslation } from 'react-i18next';
import { toast } from 'react-toastify';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import {
  Button,
  CircularProgress,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Switch,
  TextField
} from '@mui/material';
import { useAddNewUserMutation } from 'features/api/usersApi';
import { USERS } from '../../app/constants/routes.constants';
import { PhoneInput } from '../../shared/utils/customNumberFormats';

type FormValues = {
  lastName: string;
  firstName: string;
  middleName: string;
  phoneNumber: string;
  login: string;
  role: string;
  status: boolean;
};

const validationSchema = yup.object().shape({
  lastName: yup.string().required('form.errors.required'),
  firstName: yup.string().required('form.errors.required'),
  phoneNumber: yup
    .string()
    .required('form.errors.required')
    .min(16, 'form.errors.isValidPhoneNumber')
    .max(16, 'form.errors.isValidPhoneNumber'),
  login: yup
    .string()
    .email('form.errors.isEmail')
    .required('form.errors.required'),
  role: yup.string().required('form.errors.required')
});

export const NewUserForm: FC = () => {
  const { t } = useTranslation();

  const [postNewUser, { isLoading }] = useAddNewUserMutation();
  const { handleSubmit, control, watch, reset } = useForm<FormValues>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      lastName: '',
      firstName: '',
      middleName: '',
      phoneNumber: '',
      login: '',
      role: 'manager',
      status: true
    }
  });

  const onSubmit: SubmitHandler<FormValues> = ({
    login,
    status: formStatus,
    ...restData
  }) => {
    postNewUser({
      ...restData,
      login,
      password: 'string',
      status: formStatus ? 'active' : 'inactive',
      merchantId: 'adika.kz'
    })
      .then((res: any) => {
        if (res?.data?.id) {
          toast.success(
            <Trans
              i18nKey="user.new.success.link"
              values={{
                email: login
              }}
              components={[
                <Link key="email" to={`${USERS}/${login}`}>
                  email
                </Link>
              ]}
            />,
            {
              position: 'top-right',
              autoClose: 5000,
              hideProgressBar: false,
              pauseOnHover: true
            }
          );

          reset({
            lastName: '',
            firstName: '',
            middleName: '',
            phoneNumber: '',
            login: '',
            role: 'manager',
            status: true
          });
        } else {
          toast.error(res?.error?.data?.message || t('status.error'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
        }
      })
      .catch((error: any) => {
        toast.error(error?.message || t('status.error'), {
          position: 'top-right',
          autoClose: 5000,
          hideProgressBar: false,
          pauseOnHover: true
        });
      });
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <p style={{ marginTop: '40px', marginBottom: '12px' }}>
        Статус учётной записи
      </p>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={6} lg={5}>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <span>
              {watch('status')
                ? t('user.status.active')
                : t('user.status.inactive')}
            </span>
            <Controller
              name="status"
              control={control}
              render={({ field: { onBlur, onChange, value } }) => {
                return (
                  <Switch
                    onBlur={onBlur}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                      onChange(event.target.checked);
                    }}
                    checked={value}
                  />
                );
              }}
            />
          </div>
          <hr
            className="line"
            style={{ marginBottom: '24px', marginTop: '24px' }}
          />
          <Controller
            name="lastName"
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('user.new.form.lastName')}
                variant="standard"
                {...field}
                helperText=" "
              />
            )}
          />
          <Controller
            name="firstName"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('user.new.form.firstName')}
                variant="standard"
                {...field}
                helperText=" "
              />
            )}
          />
          <Controller
            name="middleName"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('user.new.form.middleName')}
                variant="standard"
                {...field}
                helperText=" "
              />
            )}
          />
          <Controller
            name="phoneNumber"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('user.new.form.phoneNumber')}
                variant="standard"
                InputLabelProps={{
                  shrink: true
                }}
                InputProps={{
                  inputComponent: PhoneInput
                }}
                helperText=" "
                {...field}
              />
            )}
          />
          <Controller
            name="login"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('user.new.form.email')}
                variant="standard"
                {...field}
                helperText=" "
              />
            )}
          />
          <InputLabel style={{ fontSize: '11px' }}>
            {t('user.new.form.role')}
          </InputLabel>
          <Controller
            name="role"
            control={control}
            render={({ field }) => (
              <Select fullWidth variant="standard" {...field}>
                <MenuItem value="">Не выбран</MenuItem>
                {[
                  {
                    value: 'admin',
                    text: t('user.role.admin')
                  },
                  {
                    value: 'manager',
                    text: t('user.role.manager')
                  },
                  {
                    value: 'courier',
                    text: t('user.role.courier')
                  }
                ].map(option => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.text}
                  </MenuItem>
                ))}
              </Select>
            )}
          />
          <Button
            type="submit"
            variant="contained"
            datatype="primary"
            disabled={isLoading}
            style={{ marginTop: '40px' }}
            fullWidth
          >
            {isLoading ? (
              <CircularProgress color="success" size={16} />
            ) : (
              t('button.add')
            )}
          </Button>
        </Grid>
      </Grid>
    </form>
  );
};
