import React, { FC, useState } from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import format from 'date-fns/format';
import parseISO from 'date-fns/parseISO';
import { IOrderInfo } from 'shared/interfaces/IOrder';
import { moneyFormatter } from 'shared/utils/formatters';
import { RootStateType } from 'features/store';
import { selectStatusesList } from 'features/slices/app-slice';
import { ChevronDownIcon, ChevronUpIcon } from '../../shared/icons';
import Typography from '@mui/material/Typography';
import { Grid, IconButton, Link } from '@mui/material';

type PropTypes = {
  item: IOrderInfo;
};

const OrderInfo: FC<PropTypes> = ({ item }) => {
  const { t } = useTranslation();
  const [expanded, setExpanded] = useState(true);
  const statusList = useSelector((state: RootStateType) =>
    selectStatusesList(state)
  );

  return (
    <>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transaction.collapse.historyOfOrder')} - {item.merchant_order_id}
        </Typography>
        <IconButton
          aria-label="filterToggle"
          onClick={() => setExpanded(prev => !prev)}
        >
          {expanded ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>
      {expanded && (
        <div style={{ backgroundColor: '#F3F4F5', padding: '24px 16px' }}>
          <Grid container>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.registrationDate')}:
                </label>
                <span className="ml-4">
                  {format(parseISO(item.created_at), 'dd.MM.yyyy, HH:mm')}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.orderAmount')}:
                </label>
                <span className="ml-4">
                  {moneyFormatter.format(item.amount)}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.installmentType')}:
                </label>
                <span className="ml-4">0-0-4</span>
              </div>
            </Grid>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transactions.filter.deliveredDate')}:
                </label>
                <span className="ml-4">
                  {format(parseISO(item.created_at), 'dd.MM.yyyy')}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.bankCommission')}:
                </label>
                <span className="ml-4">
                  {moneyFormatter.format(item.amount)}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.paymentTerms')}:
                </label>
                <Link href="https://alfabank.kz" target="_blank">
                  alfabank.kz/bnpl
                </Link>
              </div>
            </Grid>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.orderStatus')}:
                </label>
                <span
                  className={`status status-${item.app_status.toLowerCase()} bold-700`}
                >
                  {statusList[item.app_status]?.toUpperCase() ??
                    t('transactions.status.type.unexpected').toUpperCase()}
                </span>
              </div>
              <div className="collapse__box-item">
                <label data-type="secondary">
                  {t('transaction.data.amountToBePaid')}:
                </label>
                <span className="ml-4">
                  {moneyFormatter.format(item.amount)}
                </span>
              </div>
            </Grid>
          </Grid>
        </div>
      )}
    </>
  );
};

export default OrderInfo;
