import React, { FC, useState } from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Controller, useForm } from 'react-hook-form';
import clsx from 'clsx';

import { useUpdateTransactionStatusMutation } from 'features/api/transactionApi';
import { ORDER_STATUS } from 'app/constants/status.constants';
import { RootStateType } from 'features/store';
import { selectMerchant } from 'features/slices/app-slice';
import { StatusMessage } from '../../../../entities';
import {
  Button,
  CircularProgress,
  MenuItem,
  Select,
  TextField,
  Typography
} from '@mui/material';
import { AmountInput } from '../../../../shared/utils/customNumberFormats';

type PropTypes = {
  id: number;
  merchantOrderId: number;
  orderStatus: string;
  amount: number;
  title: string;
  handleClose: () => void;
};

const OrderCancel: FC<PropTypes> = ({
  id,
  merchantOrderId,
  orderStatus,
  amount,
  title,
  handleClose
}) => {
  const { t } = useTranslation();
  const { partnerCode } = useSelector((state: RootStateType) =>
    selectMerchant(state)
  );
  const {
    handleSubmit,
    control,
    formState: { errors }
  } = useForm({
    defaultValues: {
      reason: '',
      amount: ''
    }
  });

  const [options] = useState([
    {
      text: t('transactions.modal.select.cancel.byTheClient'),
      value: 'value1'
    },
    { text: t('transactions.modal.select.cancel.refused'), value: 'value2' },
    {
      text: t('transactions.modal.select.cancel.notDelivery'),
      value: 'value3'
    },
    {
      text: t('transactions.modal.select.cancel.outOfStock'),
      value: 'value4'
    }
  ]);

  const [updateStatus, { isSuccess, isError, isLoading, error }] =
    useUpdateTransactionStatusMutation();
  const [compensationType, setCompensationType] = useState('fullReturn');

  const onSubmit = (values: any) => {
    updateStatus({
      id,
      body: [
        {
          merchantId: partnerCode,
          orders: [
            {
              amount:
                compensationType === 'fullReturn' ? amount : values.amount,
              orderId: merchantOrderId,
              status: ORDER_STATUS.CANCELED
            }
          ]
        }
      ]
    });
  };

  const handleTypeChange = (type: 'fullReturn' | 'partialReturn') => {
    if (compensationType !== type) {
      setCompensationType(type);
    }
  };

  if (isSuccess) {
    return (
      <StatusMessage
        status="success"
        title={t('transactions.modal.success.canceled', {
          orderNumber: merchantOrderId
        })}
      />
    );
  }

  if (isError && error && 'data' in error) {
    //@ts-ignore
    return <StatusMessage status="error" title={error?.data?.message} />;
  }

  return (
    <>
      <Typography variant="h4" className="modal-responsive--title">
        {title} №{merchantOrderId}
      </Typography>
      <form onSubmit={handleSubmit(onSubmit)}>
        <p className="modal__subtitle">
          {t('transactions.modal.subtitle.cancel')}
        </p>

        <Controller
          name="reason"
          control={control}
          render={({ field }) => (
            <Select
              fullWidth
              variant="standard"
              {...field}
              className="select_theme_alfa-on-white select-button"
            >
              <MenuItem value="">Не выбран</MenuItem>
              {options.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.text}
                </MenuItem>
              ))}
            </Select>
          )}
        />
        {![ORDER_STATUS.READY_DELIVERY, ORDER_STATUS.COMPLETED].includes(
          orderStatus
        ) && (
          <>
            <label>{t('transactions.modal.subtitle.compensationType')}</label>
            <div>
              <Button
                className={clsx(
                  'btn-circle',
                  compensationType === 'fullReturn' && 'btn-primary'
                )}
                onClick={() => handleTypeChange('fullReturn')}
              >
                {t('transactions.modal.button.fullReturn')}
              </Button>
              <Button
                className={clsx(
                  'btn-circle',
                  compensationType === 'partialReturn' && 'btn-primary'
                )}
                onClick={() => handleTypeChange('partialReturn')}
              >
                {t('transactions.modal.button.partialReturn')}
              </Button>
            </div>
            {compensationType === 'partialReturn' && (
              <Controller
                name="amount"
                control={control}
                rules={{
                  validate: {
                    notZero: value => Number(value) !== 0,
                    notEqualToAmount: value =>
                      Number(value.toString().replace(/\s/g, '')) !== amount ||
                      `${t(
                        'transactions.modal.error.compensationEqualToAmount'
                      )}`,
                    lessThanAmount: value =>
                      Number(value.toString().replace(/\s/g, '')) < amount ||
                      `${t('transactions.modal.error.exceedAmount')}`
                  }
                }}
                render={({ field: { value, ...rest } }) => (
                  <TextField
                    fullWidth
                    label={t('transactions.modal.input.compensationAmount')}
                    variant="standard"
                    InputLabelProps={{
                      shrink: true
                    }}
                    InputProps={{
                      inputComponent: AmountInput
                    }}
                    helperText=" "
                    {...rest}
                    value={parseInt(value)}
                  />
                )}
              />
            )}
          </>
        )}
        <div style={{ display: 'flex', gap: '16px' }} className="mt-16">
          <Button type="submit" variant="contained" datatype="primary">
            {isLoading ? (
              <CircularProgress color="success" size={16} />
            ) : (
              t('button.confirm')
            )}
          </Button>
          <Button variant="contained" datatype="primary" onClick={handleClose}>
            {t('button.cancel')}
          </Button>
        </div>
      </form>
    </>
  );
};

export default OrderCancel;
