import { createSelector, createSlice, Reducer } from '@reduxjs/toolkit';
import { IError } from 'shared/interfaces/IError';
import { IStatusOption } from 'shared/interfaces/IStatus';
import type { RootStateType } from '../store';
import { partnerAPI } from '../api/partnerApi';
import { IPartner } from '../../shared/interfaces/IPartner';
import { countryApi } from '../api/countryApi';
import { ICity } from '../../shared/interfaces/ICity';

const initialState = {
  error: {} as IError,
  statuses: {
    list: {} as Record<string, string>,
    unique: [] as IStatusOption[]
  },
  cities: [] as { text: string; value: string }[],
  countries: [] as { text: string; value: string }[],
  merchant: {
    billingNumber: '',
    bin: '',
    partnerCode: '1',
    partnerEmail: '',
    partnerLegalName: '',
    partnerPoints: [],
    partnerWebsite: '',
    transitBill: ''
  } as IPartner
};
type InitialStateType = typeof initialState;

const selectApp = (state: RootStateType) => state.app;

export const selectStatuses = createSelector(
  selectApp,
  state => state.statuses
);

export const selectStatusesList = createSelector(
  selectStatuses,
  state => state.list
);
export const selectStatusesUnique = createSelector(
  selectStatuses,
  state => state.unique
);

export const selectMerchant = createSelector(
  selectApp,
  state => state.merchant
);

export const selectCountries = createSelector(
  selectApp,
  state => state.countries
);

export const selectCities = createSelector(selectApp, state => state.cities);

export const selectAppError = createSelector(selectApp, state => state.error);

const appSlice = createSlice({
  name: 'app',
  initialState,
  reducers: {
    resetState: () => initialState,
    setStatuses: (state, { payload }) => {
      state.statuses.list = payload.list;
      state.statuses.unique = payload.unique;
    },
    resetMerchant: state => {
      state.merchant = {
        billingNumber: '',
        bin: '',
        partnerCode: '1',
        partnerEmail: '',
        partnerLegalName: '',
        partnerPoints: [],
        partnerWebsite: '',
        transitBill: ''
      };
    }
  },
  extraReducers: builder => {
    builder.addMatcher(
      partnerAPI.endpoints.getPartner.matchFulfilled,
      (state, { payload }) => {
        state.merchant = payload;
      }
    );
    builder.addMatcher(
      countryApi.endpoints.getCountries.matchFulfilled,
      (state, { payload }) => {
        const newData = payload?.map((item: ICity) => {
          return {
            text: item.RUName,
            value: item.id?.toString()
          };
        });
        state.countries = newData;
      }
    );
    builder.addMatcher(
      countryApi.endpoints.getCities.matchFulfilled,
      (state, { payload }) => {
        const newData = payload?.map((item: ICity) => {
          return {
            text: item.RUName,
            value: item.id?.toString()
          };
        });
        state.cities = newData;
      }
    );
  }
});

export const { resetState, setStatuses, resetMerchant } = appSlice.actions;

export default appSlice.reducer as Reducer<InitialStateType>;
