import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { Outlet, useLocation } from 'react-router-dom';
import { Grid } from '@mui/material';
import { RootStateType } from '../../features/store';
import { selectMerchant } from '../../features/slices/app-slice';
import { Header, Navbar, Sidebar } from '../ui';
import { ROOT } from '../../app/constants/routes.constants';

const NavbarWrapper = () => {
  const { pathname } = useLocation();

  if (pathname === ROOT) {
    return <Navbar />;
  }

  return null;
};

export const MainLayout: FC = () => {
  const merchant = useSelector((state: RootStateType) => selectMerchant(state));

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={4} md={3} lg={2} xl={2}>
        <Sidebar />
      </Grid>
      <Grid item xs={12} sm={8} md={9} lg={10} xl={10}>
        <Header merchant={merchant} />
        <NavbarWrapper />
        <div className="mr-16">
          <Outlet />
        </div>
      </Grid>
    </Grid>
  );
};
