import React, { FC, useCallback, useState } from 'react';
import { Link, useNavigate, useSearchParams } from 'react-router-dom';
import { Controller, useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { format } from 'date-fns';
import { Table } from 'shared/ui/Table';
import { IColumn } from 'shared/ui/Table/types';
import { NEW_USER, USERS } from 'app/constants/routes.constants';
import { useGetUsersQuery } from 'features/api/usersApi';
import { objectClear } from '../../shared/utils/objectClear';
import { DataType } from './Users.models';
import {
  Button,
  Checkbox,
  Grid,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  Typography
} from '@mui/material';
import { ChevronDownIcon, ChevronUpIcon } from '../../shared/icons';
import { CustomPagination } from '../../shared/ui';

type PropsType = {
  title?: string | undefined;
  merchantId?: string | undefined;
};

const Users: FC<PropsType> = ({ title, merchantId }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [isFilterVisible, setIsFilterVisible] = useState(true);
  const [search, setSearch] = useSearchParams();
  const [checkedItems] = useState<Array<number | string>>([]);
  const [limit, setLimit] = useState(25);
  const [selectedItems, setSelectedItems] = useState<Array<string | number>>(
    []
  );
  const [queryParams, setQueryParams] = useState({
    fullName: search.get('fullName') ?? '',
    login: search.get('login') ?? '',
    registeredAt: '',
    merchantId: merchantId ?? '',
    role: search.get('role') ?? '',
    registeredByFIO: search.get('registeredByFIO') ?? ''
  });

  const { data, isFetching } = useGetUsersQuery(objectClear(queryParams));

  const currentPageIndex = (Number(search.get('page')) || 1) - 1;
  const pagesCount = (data && Math.ceil(data.length / limit)) || 1;

  const handleFilterToggle = () => {
    setIsFilterVisible(prev => !prev);
  };

  const handlePageChange = (
    event: React.ChangeEvent<unknown>,
    value: number
  ) => {
    setSearch(`?page=${value + 1}`);
  };

  const handleReset = useCallback(() => {
    setQueryParams({
      fullName: '',
      login: '',
      registeredAt: '',
      merchantId: merchantId ?? '',
      role: '',
      registeredByFIO: ''
    });
    setSearch('');
  }, []);

  const handleSelect =
    (selectedId: string | number) => (e:any) => {
      if (e.target.checked) {
        setSelectedItems(prev => [...prev, selectedId]);
      } else {
        setSelectedItems(prev => [
          ...prev.filter(prevId => prevId !== selectedId)
        ]);
      }
    };

  const columns: IColumn[] = [
    {
      title: '',
      grid: 0.3,
      dataIndex: 'id',
      key: 'id',
      render: (value: string) =>  (
        <Checkbox
          onChange={handleSelect(value)}
          checked={selectedItems.includes(value)}
        />
      )
    },
    {
      title: t('users.table.header.fullName'),
      dataIndex: 'firstName',
      key: 'firstName',
      grid: 1.5,
      render: (value: string, record: DataType) => (
          <Link to={`${USERS}/${record.login}`} className="primary-color">
            {record.lastName} {value} {record.middleName}
          </Link>
        )
    },
    {
      title: t('users.table.header.partner'),
      dataIndex: 'merchantId',
      key: 'merchantId'
    },
    {
      title: t('users.table.header.email'),
      dataIndex: 'login',
      key: 'login'
    },
    {
      title: t('users.table.header.role'),
      dataIndex: 'role',
      key: 'role',
      render: (value: string) =>t(`user.role.${value}`)
    },
    {
      title: t('users.table.header.dateOfRegistration'),
      dataIndex: 'registeredAt',
      key: 'registeredAt',
      render: (value: string) => format(new Date(value), 'dd.MM.yyyy')
    },
    {
      title: t('users.table.header.registeredBy'),
      dataIndex: 'registeredByFIO',
      key: 'registeredByFIO',
      render: (value: string | number) => value
    }
  ];

  const { handleSubmit, control, reset } = useForm({
    defaultValues: {
      fullName: '',
      login: '',
      registeredAt: '',
      merchantId: '',
      role: '',
      registeredByFIO: ''
    }
  });

  const onSubmit = handleSubmit(data => {
    // const registeredAt = data.registeredAt.split('.').reverse().join('-');
    // setFilter({ ...data, registeredAt });
  });

  const handleResetForm = () => {
    reset();
  };

  return (
    <>
      <Typography variant="h1">{t('users.header.title')}</Typography>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transactions.filter.title')}
        </Typography>
        <IconButton aria-label="filterToggle" onClick={handleFilterToggle}>
          {isFilterVisible ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>

      {isFilterVisible && (
        <form onSubmit={onSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="fullName"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('users.filter.fullName')}
                    variant="standard"
                    {...field}
                    helperText=" "
                  />
                )}
              />
              {/*{isDisabledMerchantId ? (*/}
              {/*  <Input*/}
              {/*    size="s"*/}
              {/*    label={t('users.filter.merchantId')}*/}
              {/*    width="available"*/}
              {/*    value={defaultValues?.merchantId}*/}
              {/*    disabled={isDisabledMerchantId}*/}
              {/*  />*/}
              {/*) : (*/}
              <Controller
                name="merchantId"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('users.filter.merchantId')}
                    variant="standard"
                    {...field}
                    helperText=" "
                  />
                )}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="login"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('users.filter.email')}
                    variant="standard"
                    {...field}
                    helperText=" "
                  />
                )}
              />
              <InputLabel style={{ fontSize: '11px' }}>
                {t('user.new.form.role')}
              </InputLabel>
              <Controller
                name="role"
                control={control}
                render={({ field }) => {
                  return (
                    <Select fullWidth variant="standard" {...field}>
                      <MenuItem value="">Не выбран</MenuItem>
                      {[
                        {
                          value: 'admin',
                          text: t('user.role.admin')
                        },
                        {
                          value: 'manager',
                          text: t('user.role.manager')
                        },
                        {
                          value: 'courier',
                          text: t('user.role.courier')
                        }
                      ].map(option => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.text}
                        </MenuItem>
                      ))}
                    </Select>
                  );
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="registeredAt"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    type="date"
                    label={t('users.filter.registeredAt')}
                    variant="standard"
                    InputLabelProps={{
                      shrink: true
                    }}
                    {...field}
                    helperText=" "
                  />
                )}
              />
              <Controller
                name="registeredByFIO"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('users.filter.registeredBy')}
                    variant="standard"
                    {...field}
                    helperText=" "
                  />
                )}
              />
            </Grid>
          </Grid>
          <div className="filter__footer">
            <Button type="submit" variant="contained" datatype="primary">
              {t('button.apply')}
            </Button>
            <Button
              type="reset"
              variant="contained"
              datatype="secondary"
              onClick={handleReset}
            >
              {t('button.reset')}
            </Button>
          </div>
        </form>
      )}
      <div style={{ display: 'flex', gap: '16px', marginBottom: '16px' }}>
        <Button
          variant="contained"
          datatype="primary"
          onClick={() => navigate(NEW_USER, { state: { merchantId } })}
        >
          {t('users.button.addUser')}
        </Button>
        {checkedItems.length !== 0 && (
          <Button variant="contained" datatype="secondary">
            {t('button.delete')}
          </Button>
        )}
      </div>
      <Table
        columns={columns}
        dataSource={data}
        isLoading={isFetching}
        limit={limit}
      />
      <CustomPagination
        limit={limit}
        total={pagesCount}
        currentIndex={(Number(search.get('page')) || 1) - 1}
        limitOptions={[20, 25, 30]}
        onChangeLimit={(event: SelectChangeEvent) => {
          if (event.target.value) {
            setLimit(Number(event.target.value) ?? 25);
            setSearch('?page=1');
          }
        }}
        onChangePage={handlePageChange}
      />
    </>
  );
};

export default Users;
