import React, { FC, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Controller, SubmitHandler, useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Button, CircularProgress, TextField } from '@mui/material';
import s from './style.module.css';
import { useLoginMutation } from 'features/api/authApi';

type FormValues = {
  email: string;
  password: string;
};

const validationSchema = yup.object().shape({
  email: yup.string().required('error'),
  password: yup.string().required('error')
});

const Login: FC = () => {
  const { t } = useTranslation();
  const [login, {isLoading, isSuccess}] = useLoginMutation();
  
  const {
    handleSubmit,
    control,
    setFocus,
    formState: { errors }
  } = useForm<FormValues>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      email: '',
      password: ''
    }
  });

  useEffect(() => {
    setFocus('email');
  }, []);

  const onSubmit: SubmitHandler<FormValues> = values => {
    login(values)
  };

  return (
    <div className="login__wrapper">
      <form onSubmit={handleSubmit(onSubmit)}>
        <h4 className={s.title}>{t('login.title')}</h4>
        <Controller
          name="email"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label="Email"
              variant="standard"
              {...field}
              error={!!errors?.email?.message}
              helperText=" "
            />
          )}
        />
        <Controller
          name="password"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              type="password"
              label={t('login.password')}
              variant="standard"
              {...field}
              error={!!errors?.password?.message}
              helperText=" "
            />
          )}
        />
        <div className={s.footer}>
          <Button type="submit" variant="contained" datatype="primary" disabled={isLoading} startIcon={isLoading && <CircularProgress color="success" size={14} />}>
            {t('login.signIn')}
          </Button>
          <Button type="button" variant="text">
            {t('login.resetPassword')}
          </Button>
        </div>
      </form>
    </div>
  );
};

export default Login;
