import {
  BaseQueryFn,
  createApi,
  FetchArgs,
  fetchBaseQuery,
  FetchBaseQueryError
} from '@reduxjs/toolkit/query/react';
import { RootStateType } from 'features/store';
import { toast } from 'react-toastify';

interface CustomError {
  data: {
    code: number;
    message: string;
  };
  status: number;
}


const dynamicBaseQuery = fetchBaseQuery({
  baseUrl: '/',
  prepareHeaders: (headers, { getState }) => {
    const { token } = (getState() as RootStateType).auth;

    if (token) {
      headers.set('token', token);
    }

    return headers;
  },
  credentials: 'include'
}) as BaseQueryFn<string | FetchArgs, unknown, CustomError>;

const baseQuery: BaseQueryFn<string | FetchArgs, unknown, CustomError> = async (
  args,
  api,
  extraOptions
) => {
  const result = await dynamicBaseQuery(args, api, extraOptions);
  if (result.error && result.error.status === 500) {
    toast.error('Ошибка на стороне сервера', {
      position: 'top-right',
      autoClose: 5000,
      hideProgressBar: false,
      pauseOnHover: true
    });
  }

  return result;
};

export const baseEmptyAPI = createApi({
  reducerPath: 'api',
  tagTypes: ['Directory', 'Transactions', 'Partners', 'User'],
  baseQuery:baseQuery,
  endpoints: () => ({})
});
