import React from 'react';
import { NumericFormat, PatternFormat } from 'react-number-format';

export function AmountInput({ inputRef, onChange, ...rest }: any) {
  return (
    <NumericFormat
      {...rest}
      getInputRef={inputRef}
      onValueChange={(values: any) =>
        onChange({
          target: {
            name: rest.name,
            value: values.value
          }
        })
      }
      decimalScale={0}
      thousandSeparator=" "
      suffix=" ₸"
    />
  );
}

export function NumberInput({
  inputRef,
  onChange,
  suffix = ' %',
  ...rest
}: any) {
  return (
    <NumericFormat
      {...rest}
      getInputRef={inputRef}
      onValueChange={(values: any) =>
        onChange({
          target: {
            name: rest.name,
            value: values.value
          }
        })
      }
      decimalScale={0}
      thousandSeparator=" "
      suffix={suffix}
    />
  );
}

export function PhoneInput({ inputRef, onChange, ...rest }: any) {
  return (
    <PatternFormat
      {...rest}
      type="tel"
      format="+# (###) ###-##-##"
      placeholder="+7 (XXX) XXX-XX-XX"
      getInputRef={inputRef}
      onValueChange={value =>
        onChange({
          target: {
            name: rest.name,
            value: value.formattedValue
          }
        })
      }
    />
  );
}
