import React, { FC } from 'react';
import Typography from '@mui/material/Typography';
import { moneyFormatter } from 'shared/utils/formatters';
import { AreaChart } from '../../shared/ui/AreaChart';
import { ArrowUpIcon } from '../../shared/icons';

type PropTypes = {
  title: string;
  value: number;
};

const AreaChartItem: FC<PropTypes> = ({ title, value }) => {
  return (
    <div className="thumbnail thumbnail__chart">
      <Typography variant="h6">{title}</Typography>
      <AreaChart />
      <label className="bold-700">{moneyFormatter.format(value)}</label>
      <div className="thumbnail__footer">
        <span className="thumbnail__profit">+20% к Июлю</span>
        <span>
          <ArrowUpIcon width={16} height={16} />
        </span>
      </div>
    </div>
  );
};

export default AreaChartItem;
