import React, { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams, Navigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { toast } from 'react-toastify';
import { yupResolver } from '@hookform/resolvers/yup';

import { useGetUserQuery, useUpdateUserMutation } from 'features/api/usersApi';
import { USERS } from '../../app/constants/routes.constants';

import { Button, CircularProgress, Typography } from '@mui/material';
import { Loader } from '../../shared/ui';

type FormValues = {
  lastName: string;
  firstName: string;
  middleName: string;
  phoneNumber: string;
  login: string;
  role: string;
  status: boolean;
  merchantId: string;
};

const validationSchema = yup.object().shape({
  lastName: yup.string().required('form.errors.required'),
  firstName: yup.string().required('form.errors.required'),
  phoneNumber: yup.string().required('form.errors.required'),
  login: yup
    .string()
    .email('form.errors.isEmail')
    .required('form.errors.required'),
  role: yup.string().required('form.errors.required')
});

const User: FC = () => {
  const { t } = useTranslation();
  const { login } = useParams();
  const [isSubmitBtnDisabled, setIsSubmitBtnDisabled] = useState(true);

  const {
    error,
    isError,
    isFetching,
    data: userData
  } = useGetUserQuery<any>(login as string);
  const [updateUser, { isLoading }] = useUpdateUserMutation();

  const { control, watch, handleSubmit, reset } = useForm<FormValues>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      lastName: '',
      firstName: '',
      middleName: '',
      phoneNumber: '',
      login: '',
      role: 'manager',
      status: true,
      merchantId: ''
    }
  });

  useEffect(() => {
    if (userData) {
      reset({
        lastName: userData?.lastName,
        firstName: userData?.firstName,
        middleName: userData?.middleName,
        phoneNumber: userData?.phoneNumber,
        login: userData?.login,
        role: userData?.role,
        status: userData?.status === 'active'
      });
    }
  }, [userData]);

  useEffect(() => {
    if (isError && !isFetching) {
      toast.error(error?.data?.message || t('status.error'), {
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        pauseOnHover: true
      });
    }
  }, [isError]);

  const onSubmit = (data: any) => {
    updateUser({
      ...data,
      login: userData.login,
      status: data.status ? 'active' : 'inactive',
      merchantId: userData.merchantId,
      jobTitle: data.role
    })
      .then((res: any) => {
        if (res?.error) {
          toast.error(res?.error?.message || t('status.error'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
          reset({
            lastName: userData?.lastName,
            firstName: userData?.firstName,
            middleName: userData?.middleName,
            phoneNumber: userData?.phoneNumber,
            login: userData?.login,
            role: userData?.role,
            status: userData?.status === 'active'
          });
        } else {
          setIsSubmitBtnDisabled(true);

          toast.success(t('data.update.success'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
        }
      })
      .catch(() => {
        toast.error(t('status.error'), {
          position: 'top-right',
          autoClose: 5000,
          hideProgressBar: false,
          pauseOnHover: true
        });
        reset({
          lastName: userData?.lastName,
          firstName: userData?.firstName,
          middleName: userData?.middleName,
          phoneNumber: userData?.phoneNumber,
          login: userData?.login,
          role: userData?.role,
          status: userData?.status === 'active'
        });
      });
  };

  if (isFetching) {
    return <Loader />;
  }

  if (isError) {
    return <Navigate to={USERS} />;
  }

  return (
    <>
      <Typography variant="h1">{t('user.single.title')}</Typography>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Button
          type="submit"
          variant="contained"
          datatype="primary"
          disabled={isLoading || isSubmitBtnDisabled}
          style={{ marginTop: '40px' }}
          fullWidth
        >
          {isLoading ? (
            <CircularProgress color="success" size={16} />
          ) : (
            t('button.save')
          )}
        </Button>
      </form>
    </>
  );
};

export default User;
