import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';

import { useUpdateTransactionStatusMutation } from 'features/api/transactionApi';
import { RootStateType } from 'features/store';
import { selectMerchant } from 'features/slices/app-slice';
import { StatusMessage } from '../../../../entities';
import { Button, CircularProgress, Typography } from '@mui/material';

type PropTypes = {
  id: number;
  title: string;
  text: string;
  successMessage: string;
  merchantOrderId: number;
};

const DeliveryToCourier: FC<PropTypes> = ({
  id,
  title,
  text,
  successMessage,
  merchantOrderId
}) => {
  const { t } = useTranslation();
  const { partnerCode } = useSelector((state: RootStateType) =>
    selectMerchant(state)
  );
  const [updateStatus, { isLoading, isSuccess }] =
    useUpdateTransactionStatusMutation();

  const handleSubmit = () => {
    updateStatus({
      id,
      body: [
        {
          merchantId: partnerCode,
          orders: [
            {
              orderId: merchantOrderId,
              status: 'readyDelivery'
            }
          ]
        }
      ]
    });
  };

  if (isSuccess) {
    return <StatusMessage status="success" title={successMessage} />;
  }

  return (
    <>
      <Typography variant="h4">{title}</Typography>
      <Typography variant="h6">{text}</Typography>
      <div className="mt-24">
        <Button
          variant="contained"
          datatype="primary"
          onClick={handleSubmit}
          disabled={isLoading}
        >
          {isLoading ? (
            <CircularProgress color="success" size={16} />
          ) : (
            t('button.send')
          )}
        </Button>
        <Button variant="contained" datatype="secondary">
          {t('button.cancel')}
        </Button>
      </div>
    </>
  );
};

export default DeliveryToCourier;
