import React, { FC, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import { useSearchParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Controller, useForm } from 'react-hook-form';
import { useAccess } from 'react-acceder';

import Typography from '@mui/material/Typography';
import { Button, IconButton, SelectChangeEvent } from '@mui/material';
import { Grid, InputLabel, MenuItem, Select, TextField } from '@mui/material';
import {
  selectMerchant,
  selectStatusesUnique
} from 'features/slices/app-slice';
import { useGetTransactionsQuery } from 'features/api/transactionApi';
import { IOrderFilter, IOrderSort } from 'shared/interfaces/IOrder';
import { RootStateType } from 'features/store';
import { phoneNumberWithoutFormat } from 'shared/utils/formatters';
import { OrderList } from './partials';
import { objectClear } from '../../shared/utils/objectClear';
import {
  PhoneInput,
  AmountInput
} from '../../shared/utils/customNumberFormats';
import { ChevronUpIcon, ChevronDownIcon } from '../../shared/icons';
import { CustomPagination } from '../../shared/ui';

import './Transactions.css';

type IFormValues = {
  orderId: string;
  id: number;
  status: string[];
  order_amount: string;
  dateCreate: string;
  deliveryDate: string;
  ph_number: string;
};

const Transactions: FC = () => {
  const { t } = useTranslation();
  const user = useAccess();
  const [search, setSearch] = useSearchParams();
  const { partnerCode } = useSelector((state: RootStateType) =>
    selectMerchant(state)
  );

  const { handleSubmit, control, reset } = useForm<IFormValues>({
    defaultValues: {
      orderId: undefined,
      id: undefined,
      status: [''],
      order_amount: undefined,
      dateCreate: '',
      deliveryDate: '',
      ph_number: ''
    }
  });

  const [queryParams, setQueryParams] = useState<IOrderFilter>({
    orderId: undefined,
    id: undefined,
    status: '',
    order_amount: '',
    dateCreate: '',
    deliveryDate: '',
    ph_number: ''
  });
  const [isFilterVisible, setIsFilterVisible] = useState(true);
  const [limit, setLimit] = useState<number>(25);
  const [tableSort, setTableSort] = useState<IOrderSort>({
    field: '',
    sort: ''
  });

  const optionValues = useSelector((state: RootStateType) =>
    selectStatusesUnique(state)
  );

  const { currentData, isFetching, isSuccess } = useGetTransactionsQuery({
    ...objectClear(queryParams),
    merchantId: partnerCode,
    sort: tableSort.field ? `${tableSort.sort},${tableSort.field}` : undefined,
    page: search.get('page') || 1,
    limit
  });

  const renderTableExport = useMemo(() => {
    return null;
  }, []);

  const handleFilterToggle = () => {
    setIsFilterVisible(prev => !prev);
  };

  const handlePageChange = (
    event: React.ChangeEvent<unknown>,
    value: number
  ) => {
    setSearch(`?page=${value + 1}`);
  };

  const handleChangeSort = (value: IOrderSort) => {
    setTableSort(value);
  };

  const onSubmit = handleSubmit((values: IFormValues) => {
    const newParams = {
      orderId: values.orderId ?? undefined,
      id: values.id ?? undefined,
      status: values.status ?? undefined,
      order_amount: values.order_amount ?? undefined,
      dateCreate: undefined,
      deliveryDate: undefined,
      ph_number: undefined
    } as unknown as IOrderFilter;

    if (values.status) {
      newParams.status = optionValues[Number(values.status)].values?.[0];
    }

    if (values.dateCreate) {
      const date = values.deliveryDate.split('.');
      newParams.dateCreate = `${date[2]}-${date[1]}-${date[0]}T00:00:00.000000`;
    }

    if (values.deliveryDate) {
      const date = values.deliveryDate.split('.');
      newParams.deliveryDate = `${date[2]}-${date[1]}-${date[0]}T00:00:00.000000`;
    }

    if (values.ph_number) {
      newParams.ph_number = phoneNumberWithoutFormat(values.ph_number);
    }

    setQueryParams(newParams);
  });

  const handleReset = () => {
    setQueryParams({
      orderId: undefined,
      id: undefined,
      status: undefined,
      order_amount: '',
      dateCreate: '',
      deliveryDate: '',
      ph_number: ''
    });
    reset();
  };

  return (
    <>
      <Typography variant="h2">{t('transactions.header.title')}</Typography>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transactions.filter.title')}
        </Typography>
        <IconButton aria-label="filterToggle" onClick={handleFilterToggle}>
          {isFilterVisible ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>
      {isFilterVisible && (
        <form onSubmit={onSubmit} className="filter">
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="orderId"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('transactions.filter.orderId')}
                    variant="standard"
                    {...field}
                    helperText=" "
                  />
                )}
              />
              <Controller
                name="dateCreate"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    type="date"
                    label={t('transactions.filter.createdDate')}
                    InputLabelProps={{
                      shrink: true
                    }}
                    variant="standard"
                    helperText=" "
                    {...field}
                  />
                )}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="id"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('transactions.filter.id')}
                    variant="standard"
                    helperText=" "
                    {...field}
                  />
                )}
              />
              <Controller
                name="deliveryDate"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    type="date"
                    label={t('transactions.filter.deliveredDate')}
                    InputLabelProps={{
                      shrink: true
                    }}
                    variant="standard"
                    helperText=" "
                    {...field}
                  />
                )}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={4} lg={4}>
              <Controller
                name="ph_number"
                control={control}
                render={({ field }) => (
                  <TextField
                    fullWidth
                    label={t('user.phoneNumber')}
                    variant="standard"
                    InputLabelProps={{
                      shrink: true
                    }}
                    InputProps={{
                      inputComponent: PhoneInput
                    }}
                    helperText=" "
                    {...field}
                  />
                )}
              />
              <Grid container spacing={3}>
                <Grid item xs={6} sm={6} lg={6}>
                  <Controller
                    name="order_amount"
                    control={control}
                    render={({ field: { value, ...rest } }) => (
                      <TextField
                        fullWidth
                        label={t('transactions.filter.amount')}
                        variant="standard"
                        InputLabelProps={{
                          shrink: true
                        }}
                        InputProps={{
                          inputComponent: AmountInput
                        }}
                        helperText=" "
                        {...rest}
                        value={parseInt(value)}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={6} sm={6} lg={6}>
                  <InputLabel style={{ fontSize: '11px' }}>
                    {t('transactions.filter.orderStatus')}
                  </InputLabel>
                  <Controller
                    name="status"
                    control={control}
                    render={({ field }) => (
                      <Select fullWidth variant="standard" {...field}>
                        <MenuItem value="">Не выбран</MenuItem>
                        {optionValues.map(option => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.text}
                          </MenuItem>
                        ))}
                      </Select>
                    )}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <div className="filter__footer">
            <Button type="submit" variant="contained" datatype="primary">
              {t('button.apply')}
            </Button>
            <Button
              type="reset"
              variant="contained"
              datatype="secondary"
              onClick={handleReset}
            >
              {t('button.reset')}
            </Button>
          </div>
        </form>
      )}
      {renderTableExport}
      {user.can('SHOW_TRANSACTION_TABLE') && (
        <OrderList
          data={currentData?.orders}
          limit={limit}
          isLoading={isFetching}
          isSuccess={isSuccess}
          orderSort={tableSort}
          handleChangeSort={handleChangeSort}
        />
      )}
      <CustomPagination
        limit={limit}
        total={currentData?.totalPages ?? 1}
        currentIndex={(Number(search.get('page')) || 1) - 1}
        limitOptions={[20, 25, 30]}
        onChangeLimit={(event: SelectChangeEvent) => {
          if (event.target.value) {
            setLimit(Number(event.target.value) ?? 25);
            setSearch('?page=1');
          }
        }}
        onChangePage={handlePageChange}
      />
    </>
  );
};

export default Transactions;
