import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { Dispatch } from 'redux';
import { CssBaseline, ThemeProvider } from '@mui/material';

import App from './app/App';
import reportWebVitals from './reportWebVitals';
import store from './features/store';
import { directoryAPI } from './features/api/directoryApi';
import { countryApi } from './features/api/countryApi';
import theme from './app/theme';

const initApp = () => async (dispatch: Dispatch<any>) => {
  await Promise.all([
    dispatch(directoryAPI.endpoints.getStatuses.initiate(''))
  ]);

  dispatch(countryApi.endpoints.getCountries.initiate(''));
  dispatch(countryApi.endpoints.getCities.initiate(''));
};

store.dispatch(initApp());

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <App />
      </ThemeProvider>
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
