import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import { RootStateType } from 'features/store';
import { ToastContainer } from 'react-toastify';
import { selectAppError } from 'features/slices/app-slice';
import TechnicalWork from 'pages/TechnicalWork/TechnicalWork';
import { LoginPage, ResetPasswordPage } from '../pages';
import configs from './enviroments';
import { TRANSACTIONS } from './constants/routes.constants';
import { MainLayout } from '../shared/layouts/MainLayout';
import { routes } from './routes';

const RouterConfig: FC = () => {
  const { code: errorCode } = useSelector((state: RootStateType) =>
    selectAppError(state)
  );

  if (errorCode) {
    return <TechnicalWork />;
  }

  return (
    <BrowserRouter basename={configs.PUBLIC_URL}>
      <ToastContainer />
      <Routes>
        <Route path="/login" element={<LoginPage />} />
        <Route path="/recovery" element={<ResetPasswordPage />} />
        <Route path="/" element={<MainLayout />}>
          {routes.map(route => {
            return (
              <Route
                key={route.path}
                path={route.path}
                element={route.element}
              />
            );
          })}
        </Route>
        <Route path="*" element={<Navigate to={TRANSACTIONS} />} />
      </Routes>
    </BrowserRouter>
  );
};

export default RouterConfig;
