import React, { FC } from 'react';
import { IconProps } from './Icon.model';
import { Icon } from './Icon';

export const ChevronUpIcon: FC<IconProps> = ({
  color = '#0A1E32',
  width,
  height,
  ...props
}) => {
  return (
    <Icon width={width} height={height} fill="none" {...props}>
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M9.99972 7.98161L15.6092 14L16 13.5806L9.99972 7.14286L4 13.5806L4.39084 14L9.99972 7.98161Z"
        fill={color}
        stroke="#0A1E32"
      />
    </Icon>
  );
};
