import React, { FC, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Controller, SubmitHandler, useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Button, TextField } from '@mui/material';
import s from './style.module.css';
import { CheckmarkDoneIcon } from '../../shared/icons';

type FormValues = {
  code: string;
  newPassword: string;
  repeatPassword: string;
};

const validationSchema = yup.object().shape({
  code: yup.string().required('error'),
  newPassword: yup.string().required('error'),
  repeatPassword: yup.string().required('error')
});

const passwordPolicies = [
  {
    id: 1,
    isChecked: false,
    text: 'Минимум 8 символов'
  },
  {
    id: 2,
    isChecked: false,
    text: 'Минимум 1 цифра и 1 буква'
  },
  {
    id: 3,
    isChecked: false,
    text: 'Не совпадает с логином'
  },
  {
    id: 4,
    isChecked: false,
    text: 'Не более 3 символов, расположенных рядом на клавиатуре'
  },
  {
    id: 5,
    isChecked: false,
    text: 'Не более 3 одинаковых символов подряд'
  },
  {
    id: 6,
    isChecked: false,
    text: 'Не повторяет старые пароли за последние 3 месяца'
  }
];

const ResetPassword: FC = () => {
  const { t } = useTranslation();
  const {
    handleSubmit,
    control,
    formState: { errors }
  } = useForm<FormValues>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      code: '',
      newPassword: '',
      repeatPassword: ''
    }
  });

  const onSubmit: SubmitHandler<FormValues> = values => {
    console.log(values);
  };

  return (
    <div className="resetPassword__wrapper">
      <form onSubmit={handleSubmit(onSubmit)}>
        <h4 className={s.title}>{t('resetPassword.title')}</h4>
        <Controller
          name="code"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label="Email"
              variant="standard"
              {...field}
              error={!!errors?.code?.message}
              helperText=" "
            />
          )}
        />
        <Controller
          name="newPassword"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              type="password"
              label={t('login.password')}
              variant="standard"
              {...field}
              error={!!errors?.newPassword?.message}
              helperText=" "
            />
          )}
        />
        <Controller
          name="repeatPassword"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              type="password"
              label={t('login.password')}
              variant="standard"
              {...field}
              error={!!errors?.repeatPassword?.message}
              helperText=" "
            />
          )}
        />
        <ul className={s.policies}>
          {passwordPolicies.map(policy => {
            return (
              <li key={policy.id}>
                <CheckmarkDoneIcon width={16} height={16} />
                <span>{policy.text}</span>
              </li>
            );
          })}
        </ul>
        <div className={s.footer}>
          <Button type="submit" variant="contained" datatype="primary">
            {t('login.signIn')}
          </Button>
          <Button type="button" variant="text">
            {t('login.resetPassword')}
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ResetPassword;
