import React, { FC, Fragment, useEffect, useMemo, useState } from 'react';
import { Navigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useForm } from 'react-hook-form';
import { toast } from 'react-toastify';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import {
  useGetPartnerQuery,
  useUpdatePartnerMutation
} from '../../../features/api/partnerApi';
import GeneralInfo from './GeneralInfo';
import LegalAddress from './LegalAddress';
import MailingAddress from './MailingAdress';
import { PARTNERS } from '../../../app/constants/routes.constants';
import { digitRegExp } from '../../../shared/utils/regexp';
import {
  Button,
  Checkbox,
  CircularProgress,
  FormControlLabel
} from '@mui/material';
import { Loader } from '../../../shared/ui';

type PropsType = {
  merchantId: string;
};

const validationSchema = yup.object().shape({
  partnerLegalName: yup.string().required('form.errors.required'),
  bin: yup
    .string()
    .required('form.errors.required')
    .min(12, 'form.errors.isNumber12')
    .max(12, 'form.errors.isNumber12')
    .matches(digitRegExp, 'form.errors.isNumber12'),
  Adresses: yup.array().of(
    yup.object().shape({
      city: yup.string().required('form.errors.required'),
      postIndex: yup.string().required('form.errors.required'),
      house: yup.string().required('form.errors.required')
    })
  )
});

const Information: FC<PropsType> = ({ merchantId }) => {
  const { t } = useTranslation();
  const [countAddress, setCountAddress] = useState<number>(0);
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [isMAddressEditable, setIsMAddressEditable] = useState(true);

  const {
    data,
    isError,
    isFetching: isMerchantLoading
  } = useGetPartnerQuery(merchantId);
  const [updatePartner, { isLoading, isSuccess }] = useUpdatePartnerMutation();

  const { handleSubmit, control, register, reset } = useForm({
    resolver: yupResolver(validationSchema)
  });

  useEffect(() => {
    if (data) {
      reset({
        ...data,
        merchantId: data?.partnerCode,
        mSite: data?.partnerWebsite,
        Adresses: data?.addresses
      });

      if (data?.addresses?.length > 1) {
        setCountAddress(data.addresses.length - 1);
      }
    }
  }, [data, reset]);

  const onSubmit = handleSubmit((values: any) => {
    updatePartner({ ...values, status: 'active', merchantId })
      .then(({ error }: any) => {
        if (error) {
          toast.error(error?.data?.errorMessage || t('status.error'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
          reset({
            ...data,
            merchantId: data?.partnerCode,
            mSite: data?.partnerWebsite,
            Adresses: data?.addresses
          });
        } else {
          setIsMAddressEditable(true);

          toast.success(t('data.update.success'), {
            position: 'top-right',
            autoClose: 5000,
            hideProgressBar: false,
            pauseOnHover: true
          });
        }
        setIsButtonDisabled(true);
      })
      .catch(() => {
        toast.error(t('status.error'), {
          position: 'top-right',
          autoClose: 5000,
          hideProgressBar: false,
          pauseOnHover: true
        });
        reset({
          ...data,
          merchantId: data?.partnerCode,
          mSite: data?.partnerWebsite,
          Adresses: data?.addresses
        });
      });
  });

  const handleIsAddressMatchChange = () => {
    setCountAddress((prev: number) => (prev !== 0 ? 0 : prev + 1));
    setIsButtonDisabled(false);
    setIsMAddressEditable(false);
  };

  const handleAddAddress = () => {
    setCountAddress(prev => prev + 1);
  };

  const generalInfoMemo = useMemo(() => {
    return (
      <GeneralInfo
        control={control}
        isButtonDisabled={isButtonDisabled}
        setIsButtonDisabled={setIsButtonDisabled}
        isEditable
      />
    );
  }, [control, isButtonDisabled]);

  const legalAddressMemo = useMemo(() => {
    return (
      <LegalAddress
        control={control}
        isButtonDisabled={isButtonDisabled}
        setIsButtonDisabled={setIsButtonDisabled}
        isEditable
      />
    );
  }, [control]);

  if (isError) {
    return <Navigate to={PARTNERS} />;
  }

  if (isMerchantLoading) {
    return <Loader />;
  }

  return (
    <form key={`${isSuccess}`} onSubmit={onSubmit} className="mt-24 mb-24">
      {generalInfoMemo}
      {legalAddressMemo}
      <div className="mb-34">
        <FormControlLabel
          control={
            <Checkbox
              onChange={handleIsAddressMatchChange}
              checked={!countAddress}
            />
          }
          label={t('partner.new.form.legal.matchEmail')}
        />
      </div>
      {!!countAddress && (
        <>
          {Array.from({ length: countAddress }, (_, index) => (
            <Fragment key={index + 1}>
              <input
                type="hidden"
                value="postal"
                {...register(`Adresses.${index + 1}.type` as never)}
              />
              <MailingAddress
                counter={index + 1}
                control={control}
                isButtonDisabled={isButtonDisabled}
                setIsButtonDisabled={setIsButtonDisabled}
                isEditable={isMAddressEditable}
              />
            </Fragment>
          ))}
          <div className="mt-24 mb-34">
            <Button
              variant="contained"
              datatype="primary"
              onClick={handleAddAddress}
            >
              + {t('partner.address.add')}
            </Button>
          </div>
        </>
      )}
      <Button
        type="submit"
        variant="contained"
        datatype="primary"
        disabled={isLoading || isButtonDisabled}
      >
        {isLoading ? (
          <CircularProgress color="success" size={16} />
        ) : (
          t('button.save')
        )}
      </Button>
    </form>
  );
};

export default Information;
