import { createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    primary: {
      main: '#556cd6'
    },
    secondary: {
      main: '#19857b'
    }
  },
  components: {
    MuiGrid: {
      styleOverrides: {
        root: {
          paddingTop: 0
        }
      }
    },
    MuiTypography: {
      variants: [
        {
          props: { variant: 'h1' },
          style: {
            fontSize: '40px',
            lineHeight: '48px',
            fontWeight: 600
          }
        },
        {
          props: { variant: 'h2' },
          style: {
            fontSize: '32px',
            lineHeight: '40px',
            fontWeight: 600
          }
        },
        {
          props: { variant: 'subtitle1' },
          style: {
            fontSize: '20px',
            lineHeight: '24px',
            fontWeight: 600
          }
        }
      ]
    },
    MuiCheckbox: {
      styleOverrides: {
        root: {
          '&.Mui-checked': {
            color: '#27AE60'
          }
        }
      }
    },
    MuiSwitch: {
      styleOverrides: {
        colorPrimary: {
          '&.Mui-checked': {
            color: '#fff'
          }
        },
        track: {
          '.Mui-checked.Mui-checked + &': {
            backgroundColor: '#27AE60'
          }
        }
      }
    },
    MuiButton: {
      styleOverrides: {
        root: {
          display: 'block',
          textTransform: 'none',
          boxShadow: 'none',
          borderRadius: 8,
          boxSizing: 'border-box',
          fontSize: '14px',
          fontWeight: '500',
          fontFamily: 'Roboto',
          lineHeight: '20px',
          padding: '8px 24px',

          "& .MuiButton-startIcon": {
            display: 'inline-block',
            verticalAlign:'middle'
          }
        }
      },
      variants: [
        {
          props: { variant: 'text' },
          style: {
            color: '#27AE60'
          }
        },
        {
          props: { variant: 'contained', datatype: 'primary' },
          style: {
            backgroundColor: '#27AE60',
            color: '#FFFFFF',

            '&:hover': {
              boxShadow: 'none',
              backgroundColor: '#27AE60',
              opacity: 0.8
            }
          }
        },
        {
          props: { variant: 'contained', datatype: 'secondary' },
          style: {
            color: '#27AE60',
            backgroundColor: '#FFFFFF',
            border: '1px solid #27AE60',

            '&:hover': {
              boxShadow: 'none',
              backgroundColor: '#FFFFFF'
            }
          }
        },
        {
          props: { variant: 'contained', disabled: true },
          style: {
            backgroundColor: '#B3DAF5',
            color: '#F5F5F5'
          }
        }
      ]
    }
  }
});

export default theme;
