import React, { FC } from 'react';
import { IPartner } from 'shared/interfaces/IPartner';
import { LogoutIcon, NotificationIcon } from '../../icons';
import s from './Header.module.css';

type PropTypes = {
  merchant: IPartner;
};

const Header: FC<PropTypes> = ({ merchant }) => {
  return (
    <header className={s.header}>
      <NotificationIcon width={24} height={28} />
      <span className={s.title}>
        {merchant?.partnerLegalName}NoName Company
      </span>
      <LogoutIcon width={21} height={22} />
    </header>
  );
};

export default Header;
