import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Control, Controller } from 'react-hook-form';
import { Grid, InputLabel, MenuItem, Select, TextField } from '@mui/material';

type PropsType = {
  control: Control<any>;
};

const Filter: FC<PropsType> = ({ control }) => {
  const { t } = useTranslation();

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={6} md={4} lg={4}>
        <Controller
          name="partnerLegalName"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label={t('partner.table.companyName')}
              variant="standard"
              {...field}
              helperText=" "
            />
          )}
        />
        <InputLabel style={{ fontSize: '11px' }}>
          {t('partner.table.status')}
        </InputLabel>
        <Controller
          name="status"
          control={control}
          render={({ field }) => (
            <Select fullWidth variant="standard">
              <MenuItem value="">Не выбран</MenuItem>
              <MenuItem value="active">{t('user.status.active')}</MenuItem>
              <MenuItem value="inactive">{t('user.status.inactive')}</MenuItem>
            </Select>
          )}
        />
      </Grid>
      <Grid item xs={12} sm={6} md={4} lg={4}>
        <Controller
          name="bin"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label={t('partner.table.bin')}
              variant="standard"
              {...field}
              helperText=" "
            />
          )}
        />
        <Controller
          name="pointCode"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label={t('partner.table.pointCode')}
              variant="standard"
              {...field}
              helperText=" "
            />
          )}
        />
      </Grid>
      <Grid item xs={12} sm={6} md={4} lg={4}>
        <Controller
          name="merchantId"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label={t('partner.table.partnerCode')}
              variant="standard"
              {...field}
              helperText=" "
            />
          )}
        />
      </Grid>
    </Grid>
  );
};

export default Filter;
