import React, { FC, useCallback, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useGetTransactionByIdQuery } from 'features/api/transactionApi';
import { FINAL_ORDER_STATUSES } from 'app/constants/status.constants';
import { ModalType } from '../Transactions/Transactions.model';
import {
  OrderCancel,
  DeliveryToCourier,
  PopConfirm,
  SmsConfirm
} from '../Transactions/partials';
import { Loader } from '../../shared/ui';
import {
  Box,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  Typography
} from '@mui/material';
import {
  CheckmarkIcon,
  CloseIcon,
  CrossHeavyIcon,
  PencilHeavyIcon
} from '../../shared/icons';
import {
  OrderInfo,
  BuyerInfo,
  OrderComposition,
  OrderHistory
} from '../../entities';
import './Transaction.css';

const Transaction: FC = () => {
  const { id } = useParams();

  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [modalType, setModalType] = useState<ModalType>();

  const { data, isLoading, isSuccess } = useGetTransactionByIdQuery(id);

  const handleModalOpen = (type: ModalType) => (): void => {
    setOpen(true);
    setModalType(type);
  };

  const handleModalClose = useCallback(() => {
    setOpen(false);
  }, [setOpen]);

  const renderModalContent = useCallback(
    (type: ModalType) => {
      if (data) {
        switch (type) {
          case 'CONFIRM_CANCEL':
            return (
              <OrderCancel
                id={data.orderInfo.id}
                merchantOrderId={data.orderInfo.merchant_order_id}
                orderStatus={data.orderInfo.app_status}
                amount={data.orderInfo.amount}
                title={t('transactions.modal.title.cancelOrder')}
                handleClose={handleModalClose}
              />
            );
          case 'DELIVERY_TO_COURIER':
            return (
              <DeliveryToCourier
                id={data.orderInfo.id}
                merchantOrderId={data.orderInfo.merchant_order_id}
                title={t('transactions.modal.title.sendForDelivery')}
                text={t('transactions.modal.text.sendForDelivery')}
                successMessage={t('transactions.modal.title.sentForDelivery')}
              />
            );
          case 'DELIVERY_TO_CLIENT':
            return (
              <PopConfirm
                title={t('transactions.modal.title.sendForDelivery')}
                text={t('transactions.modal.text.sendForDelivery')}
                okText={t('button.send')}
                cancelText={t('button.cancel')}
                onCancel={handleModalClose}
              >
                <SmsConfirm
                  order={data.orderInfo}
                  successMessage={t('transactions.modal.success.delivered')}
                />
              </PopConfirm>
            );
          default:
            return null;
        }
      }

      return null;
    },
    [data, t, handleModalClose]
  );

  if (isLoading) {
    return <Loader />;
  }

  if (isSuccess && data) {
    return (
      <>
        {data.orderInfo?.app_status && data.orderInfo?.merchant_order_id && (
          <div className="mb-32 d-flex mobile-block">
            <Typography variant="h2" className="mr-24">
              {t('transaction.header.title')} №{data.orderInfo.id}
            </Typography>
            {FINAL_ORDER_STATUSES.includes(data.orderInfo.app_status) && (
              <>
                <Button
                  type="button"
                  variant="text"
                  onClick={() => setIsEdit(prev => !prev)}
                >
                  <PencilHeavyIcon width={16} height={16} />
                  {t('transaction.buttons.edit')}
                </Button>
                <Button
                  type="button"
                  variant="text"
                  onClick={handleModalOpen(
                    data.orderInfo.app_status === 'new'
                      ? 'DELIVERY_TO_COURIER'
                      : 'DELIVERY_TO_CLIENT'
                  )}
                >
                  <CheckmarkIcon width={16} height={11} />
                  {t('transaction.buttons.handOver')}
                </Button>
                <Button
                  type="button"
                  variant="text"
                  onClick={handleModalOpen('CONFIRM_CANCEL')}
                >
                  <CrossHeavyIcon width={14} height={14} />
                  {t('transaction.buttons.cancel')}
                </Button>
              </>
            )}
          </div>
        )}
        <OrderInfo item={data.orderInfo} />
        <BuyerInfo item={data.clientInfo} />
        <OrderComposition applicationDetail={data.applicationDetail} />
        <OrderHistory
          order={data.changeHistory}
          appStatus={data.orderInfo.app_status}
        />
        <Dialog open={open} onClose={handleModalClose}>
          <DialogTitle>
            <Box display="flex" justifyContent="end">
              <IconButton onClick={handleModalClose}>
                <CloseIcon width={16} height={16} />
              </IconButton>
            </Box>
          </DialogTitle>
          <DialogContent>{renderModalContent(modalType)}</DialogContent>
        </Dialog>
      </>
    );
  }

  return null;
};

export default Transaction;
