import React, { FC, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Controller, useForm, SubmitHandler } from 'react-hook-form';
import { Button, CircularProgress, TextField, Typography } from '@mui/material';
import { IOrderInfo } from 'shared/interfaces/IOrder';
import {
  usePostCheckOtpMutation,
  usePostSendOtpMutation
} from 'features/api/transactionApi';
import { RootStateType } from 'features/store';
import { selectMerchant } from 'features/slices/app-slice';
import { StatusMessage } from '../../../../entities';

type PropTypes = {
  order: IOrderInfo;
  successMessage: string;
};

const SmsConfirm: FC<PropTypes> = ({ order, successMessage }) => {
  const { t } = useTranslation();
  const { partnerCode } = useSelector((state: RootStateType) =>
    selectMerchant(state)
  );
  const { handleSubmit, control } = useForm<{ otp: string }>();
  const lastMutation = useRef<any>(undefined);
  const [
    postSendOtp,
    {
      isLoading: isSendingOtp,
      isSuccess: hasSentOtp,
      isError: isFailedOtp,
      error: errorOtp
    }
  ] = usePostSendOtpMutation();
  const [
    postCheckOtp,
    {
      isLoading: isCheckingOtp,
      isSuccess: hasCheckedOtp,
      isError: isErrorCheckOtp
    }
  ] = usePostCheckOtpMutation();

  useEffect(() => {
    postSendOtp({
      id: order.id,
      body: {
        merchantOrderId: order.merchant_order_id,
        merchantId: partnerCode,
        amount: order.amount
      }
    });
  }, [
    postSendOtp,
    order.id,
    order.merchant_order_id,
    order.amount,
    partnerCode
  ]);

  const handleOtpResend = () => {
    postSendOtp({
      id: order.id,
      body: {
        merchantOrderId: order.merchant_order_id,
        merchantId: partnerCode,
        amount: order.amount
      }
    });
    lastMutation.current?.unsubscribe();
  };

  const onSubmit: SubmitHandler<{ otp: string }> = ({ otp }) => {
    lastMutation.current = postCheckOtp({
      id: order.id,
      body: {
        merchantOrderId: order.merchant_order_id,
        merchantId: partnerCode,
        amount: order.amount,
        otp
      }
    });
  };

  if (hasCheckedOtp) {
    return <StatusMessage status="success" title={successMessage} />;
  }

  if (isFailedOtp && errorOtp && 'data' in errorOtp) {
    //@ts-ignore
    return <StatusMessage status="error" title={errorOtp?.data?.message} />;
  }

  return (
    <>
      <Typography variant="h2" className="modal-responsive--title">
        {t('transactions.modal.title.confirmDelivery')}
      </Typography>
      <p className="modal-responsive--subtitle">
        {t('transactions.modal.subtitle.confirmDelivery', {
          orderNumber: order.merchant_order_id
        })}
      </p>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Controller
          name="otp"
          control={control}
          render={({ field }) => (
            <TextField
              fullWidth
              label={t('transactions.modal.input.smsCode')}
              variant="standard"
              {...field}
              helperText=" "
            />
          )}
        />
        <div className="modal-responsive__footer">
          <Button
            type="submit"
            variant="contained"
            data-type="primary"
            disabled={isErrorCheckOtp || isCheckingOtp}
          >
            {isCheckingOtp ? (
              <CircularProgress color="success" size={32} />
            ) : (
              t('button.confirm')
            )}
          </Button>
          <Button
            variant="contained"
            data-type="secondary"
            onClick={handleOtpResend}
            disabled={isSendingOtp}
          >
            {isCheckingOtp ? (
              <CircularProgress color="success" size={32} />
            ) : (
              t('button.resend')
            )}
          </Button>
        </div>
      </form>
    </>
  );
};

export default SmsConfirm;
