import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { IOrderDetail } from '../../shared/interfaces/IOrder';
import { ChevronDownIcon, ChevronUpIcon } from '../../shared/icons';
import { IconButton, TextField, Typography } from '@mui/material';

type PropTypes = {
  applicationDetail: IOrderDetail[];
};

const OrderComposition: FC<PropTypes> = ({ applicationDetail }) => {
  const { t } = useTranslation();
  const [expanded, setExpanded] = useState(true);

  return (
    <>
      <div className="filter__header">
        <Typography variant="subtitle1">
          {t('transaction.collapse.orderComposition')}
        </Typography>
        <IconButton
          aria-label="filterToggle"
          onClick={() => setExpanded(prev => !prev)}
        >
          {expanded ? (
            <ChevronDownIcon width={20} height={20} />
          ) : (
            <ChevronUpIcon width={20} height={20} />
          )}
        </IconButton>
      </div>

      {expanded && (
        <div className="overflowX">
          <table className="table">
            <thead>
              <tr>
                <td>{t('transaction.data.title')}</td>
                <td>{t('transaction.data.vendorCode')}</td>
                <td>{t('transaction.data.numberOfGoods')}</td>
                <td>{t('transaction.data.price')}</td>
                <td>{t('transaction.data.amount')}</td>
                <td> </td>
              </tr>
            </thead>
            <tbody>
              {applicationDetail?.map(item => (
                <tr key={item.itemId}>
                  <td>{item.itemName}</td>
                  <td>0000123498</td>
                  <td width={64}>{item.itemCount} шт</td>
                  <td>{item.itemPrice} ₸</td>
                  <td>{item.itemTotal} ₸</td>
                  <td> </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </>
  );
};

export default OrderComposition;
