import React, { Dispatch, FC, SetStateAction, useState } from 'react';
import { useSelector } from 'react-redux';
import { Control, Controller } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import {
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography
} from '@mui/material';
import { RootStateType } from '../../../features/store';
import { selectCities } from '../../../features/slices/app-slice';

type PropsType = {
  counter?: number;
  control: Control<any>;
  isEditable?: boolean;
  isButtonDisabled?: boolean;
  setIsButtonDisabled?: Dispatch<SetStateAction<boolean>>;
};

const MailingAddress: FC<PropsType> = ({
  counter = 0,
  control,
  isEditable = false,
  isButtonDisabled = true,
  setIsButtonDisabled
}) => {
  const { t } = useTranslation();
  const cityList = useSelector((state: RootStateType) => selectCities(state));

  const [disabledFields, setDisabledFields] = useState({
    city: isEditable,
    postIndex: isEditable,
    street: isEditable,
    house: isEditable,
    flat: isEditable,
    okato: isEditable
  });

  const handleEdit = (fieldName: string) => () => {
    setDisabledFields(prev => ({ ...prev, [fieldName]: false }));

    if (isButtonDisabled && setIsButtonDisabled) {
      setIsButtonDisabled(false);
    }
  };

  return (
    <>
      <Typography variant="subtitle1">
        {t('partner.new.header.mailingAddress')}
      </Typography>

      <Grid container className="mt-16 mb-24">
        <Grid item xs={12} sm={6} md={4} lg={4}>
          <InputLabel style={{ fontSize: '11px' }}>
            {t('partner.new.form.legal.city')}
          </InputLabel>
          <Controller
            name={`Adresses.${counter}.city`}
            control={control}
            render={({ field, fieldState: { error } }) => (
              <Select
                fullWidth
                variant="standard"
                {...field}
                error={!!error?.message}
              >
                <MenuItem value="">Не выбран</MenuItem>
                {cityList.map(entityForm => (
                  <MenuItem key={entityForm.value} value={entityForm.value}>
                    {entityForm.text}
                  </MenuItem>
                ))}
              </Select>
            )}
          />

          <Controller
            name={`Adresses.${counter}.postIndex`}
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.legal.postalCode')}
                variant="standard"
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />

          <Controller
            name={`Adresses.${counter}.street`}
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.legal.street')}
                variant="standard"
                helperText=" "
                {...field}
              />
            )}
          />

          <Controller
            name={`Adresses.${counter}.house`}
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.legal.house')}
                variant="standard"
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />

          <Controller
            name={`Adresses.${counter}.flat`}
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.legal.office')}
                variant="standard"
                helperText=" "
                {...field}
              />
            )}
          />
          <Controller
            name={`Adresses.${counter}.okato`}
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.legal.okato')}
                variant="standard"
                helperText=" "
                {...field}
              />
            )}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default MailingAddress;
