import React, { Dispatch, FC, SetStateAction, useState } from 'react';
import { Control, Controller } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import {
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography
} from '@mui/material';
import { legalEntityForm } from '../../../app/constants/merchant.constants';
import { PhoneInput } from '../../../shared/utils/customNumberFormats';

type PropsType = {
  control: Control<any>;
  isEditable?: boolean;
  isButtonDisabled?: boolean;
  setIsButtonDisabled?: Dispatch<SetStateAction<boolean>>;
};

const GeneralInfo: FC<PropsType> = ({
  control,
  isEditable = false,
  isButtonDisabled = true,
  setIsButtonDisabled
}) => {
  const { t } = useTranslation();
  const [disabledFields, setDisabledFields] = useState({
    partnerLegalName: isEditable,
    legalEntityForm: isEditable,
    bin: isEditable,
    mSite: isEditable,
    pointCode: isEditable,
    phoneNumber: isEditable,
    archivePassword: isEditable,
    merchantId: isEditable
  });

  const handleEdit = (fieldName: string) => () => {
    setDisabledFields(prev => ({ ...prev, [fieldName]: false }));

    if (isButtonDisabled && setIsButtonDisabled) {
      setIsButtonDisabled(false);
    }
  };

  return (
    <>
      <Typography variant="subtitle1">
        {t('partner.new.header.general')}
      </Typography>
      <Grid container className="mb-24 mt-16" spacing={3}>
        <Grid item xs={12} sm={6} md={4} lg={4}>
          <Controller
            name="partnerLegalName"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.legalName')}
                variant="standard"
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />
          <Controller
            name="pointCode"
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.ukkNumber')}
                variant="standard"
                helperText={t('hint.ukkNumber')}
                {...field}
              />
            )}
          />
          <Controller
            name="merchantId"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.merchantId')}
                variant="standard"
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />
          <Controller
            name="bin"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.bin')}
                variant="standard"
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />
          <Controller
            name="mSite"
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.website')}
                variant="standard"
                helperText=" "
                {...field}
              />
            )}
          />
          <Controller
            name="phoneNumber"
            control={control}
            render={({ field, fieldState: { error } }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.phoneNumber')}
                variant="standard"
                InputLabelProps={{
                  shrink: true
                }}
                InputProps={{
                  inputComponent: PhoneInput
                }}
                helperText={error?.message ? t(error.message) : ' '}
                error={!!error?.message}
                {...field}
              />
            )}
          />
          <Controller
            name="archivePassword"
            control={control}
            render={({ field }) => (
              <TextField
                fullWidth
                label={t('partner.new.form.general.archiveCode')}
                variant="standard"
                helperText={t('hint.ukkDB')}
                {...field}
              />
            )}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={4} lg={4}>
          <InputLabel style={{ fontSize: '11px' }}>
            {t('partner.new.form.general.entityForm')}
          </InputLabel>
          <Controller
            name="legalEntityForm"
            control={control}
            render={({ field }) => (
              <Select fullWidth variant="standard" {...field}>
                {legalEntityForm.map(entityForm => (
                  <MenuItem key={entityForm.value} value={entityForm.value}>
                    {entityForm.text}
                  </MenuItem>
                ))}
              </Select>
            )}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default GeneralInfo;
