import React, { FC, ReactElement, useState } from 'react';
import { Button, Typography } from '@mui/material';

type PropTypes = {
  title: string;
  text: string;
  okText: string;
  cancelText: string;
  onCancel: () => void;
  children: ReactElement;
};

const PopConfirm: FC<PropTypes> = ({
  title,
  text,
  okText,
  cancelText,
  onCancel,
  children
}) => {
  const [isShowChildren, setIsShowChildren] = useState(false);

  const handleButtonClick = () => {
    setIsShowChildren(true);
  };

  if (isShowChildren) {
    return children;
  }

  return (
    <>
      <Typography variant="h4" className="modal-responsive--title">
        {title}
      </Typography>
      <Typography variant="h6">{text}</Typography>
      <div className="mt-24">
        <Button
          type="submit"
          variant="contained"
          data-type="primary"
          onClick={handleButtonClick}
        >
          {okText}
        </Button>
        <Button variant="contained" data-type="secondary" onClick={onCancel}>
          {cancelText}
        </Button>
      </div>
    </>
  );
};

export default PopConfirm;
