import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { useLocation, useNavigate } from 'react-router-dom';

import {
  ANALYTICS,
  SETTLEMENTS,
  STATISTICS,
  TRANSACTIONS
} from 'app/constants/routes.constants';
import { TabsType } from './model';
import { Box, Tab, Tabs } from '@mui/material';

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

const Navbar: FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { pathname } = useLocation();

  const tabs: TabsType = {
    tab_1: {
      title: 'page.nav.tab_1',
      url: STATISTICS
    },
    tab_2: {
      title: 'page.nav.tab_2',
      url: TRANSACTIONS
    },
    tab_3: {
      title: 'page.nav.tab_3',
      url: ANALYTICS
    },
    tab_4: {
      title: 'page.nav.tab_4',
      url: SETTLEMENTS
    }
  };

  const handleClick = (event: any) => {
    event.preventDefault();
    if (event.target.getAttribute('href')) {
      navigate(event.target.getAttribute('href'), { replace: true });
    } else {
      navigate(pathname, { replace: true });
    }
  };

  return (
    <Box sx={{ borderBottom: 1, borderColor: 'divider' }} className="navbar">
      <Tabs value={pathname} onChange={handleClick} aria-label="partner">
        {Object.keys(tabs).map(key => (
          <Tab key={key} label={t(`page.nav.${key}`)} {...a11yProps(1)} />
        ))}
      </Tabs>
    </Box>
  );
};

export default Navbar;
