import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';

import styles from './TechnicalWork.module.css';
import {
  MobileTechnicalWorkIcon,
  DesktopTechnicalWorkIcon
} from '../../shared/icons';
import { Button, Typography } from '@mui/material';

const TechnicalWork: FC = () => {
  const { t } = useTranslation();

  return (
    <div className={styles.container}>
      <div className="mobile-only">
        <MobileTechnicalWorkIcon
          width={375}
          height={350}
          className={styles.svg}
        />
      </div>
      <div className="desktop-only">
        <DesktopTechnicalWorkIcon
          width={1920}
          height={1080}
          className={styles.svg}
        />
      </div>
      <div className={styles.footer}>
        <Typography variant="h1" className={styles.title}>
          {t('page.error.tech_work.title')}
        </Typography>
        <Typography className={styles.text}>
          {t('page.error.tech_work.text')}
        </Typography>

        <div className={styles.button}>
          <Button variant="contained" data-type="primary">
            {t('page.error.tech_work.button')}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default TechnicalWork;
